.search input[type=puuid] {
 
    font-family: 'Montserrat', sans-serif;
    outline: 0;
    width: 33%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 16px;
    font-weight: 300;
    background-color: rgba(255, 255, 255, 0.07);
    color: white;

  
}

.search button{


  font-family: 'Montserrat', sans-serif;
  background: #3c31dd;
  border: 0;
  padding: 15px;
  color: white;
  font-size: 16px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
  margin-top: 10px;
  margin-right: 10px;
}



.search button:hover {
    background: #837bea;
}

.search select   {  
  font-family: 'Montserrat', sans-serif;
  outline: 0;
  
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 300;
  background-color: rgba(255, 255, 255, 0.07);
  color: white;
}

.search select option {
  background-color: #282c34;
  color: white;
}


.rankInfo {
  font-family: 'Montserrat', sans-serif;
 
  position: relative;
  z-index: 1;
  background-color: rgba(32, 39, 49, 0.13);
  border-radius: 8px;
  max-width: 15%;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 50px 35px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);

  display:flex;
  flex-direction: column; /* Stack items vertically */
  align-items: center;



}



